import React, { Component } from 'react';
import './CommonTop.css'

export default class CommonTop extends Component{
	render(){
		return (
			<div className="sy_top">
				<div className="sy_top_c base">
					<p className="">-IT</p>
					<i className="call">400-811-9990</i>
					<ul className="">
						<li><a rel="nofollow" href="http://www.goodprogrammer.org/" target="_blank" style={{color:'#f00'}} ></a></li>
						<li><a rel="nofollow" href="http://www.codingke.com/" target="_blank" style={{color:'#f00'}}></a></li>
						<li><a href="http://www.mobiletrain.org/about/contact.html" target="_blank"></a></li>
						<li id="tebie"><a href="http://www.mobiletrain.org/" onClick={() => false}></a></li>
					</ul>
				</div>
			</div>
		)
	}
}


